import {createRouter, createWebHistory, RouteRecordRaw} from 'vue-router'
import main from './../views'
import {getIndexDB} from "@/utils/indexDB";
import {STORAGE_KEYS} from "@/const";

const routes: Array<RouteRecordRaw> = [
	{
		path: '/',
		redirect: '/user'
	},
	{
		path: '/user',
		name: 'home',
		component: main,
		children: [
			{
				path: '',
				name: 'user-index',
				component: () => import(/* webpackChunkName:"user.index" */ '@/views/user')
			},
			{
				path: 'info',
				name: 'user-info',
				component: () => import(/* webpackChunkName:"user.index" */ '@/views/user/info')
			}
		]
	},
	{
		path: '/claims',
		component: main,
		children: [
			{
				path: '',
				component: () => import(/* webpackChunkName:"claims.list" */ '@/views/claims')
			}
		]
	},
	{
		path: '/login',
		name: 'login',
		component: () => import(/* webpackChunkName: "login" */ '../views/login')
	},
	// {
	//   path: '/about',
	//   name: 'about',
	//   // route level code-splitting
	//   // this generates a separate chunk (about.[hash].js) for this route
	//   // which is lazy-loaded when the route is visited.
	//   component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
	// }
]

const router = createRouter({
	history: createWebHistory(process.env.BASE_URL),
	routes
})
router.beforeEach((to, from, next) => {
	/* eslint-disable */
	getIndexDB(STORAGE_KEYS.LOGGED_IN_USER)
		.then((result: any) => {
			if (!result?.token && to.path !== '/login') {
				next({path: '/login'})
			} else if ((result?.token && to.path !== '/login')||(!result?.token && to.path === '/login')) {
				next()
			} else {
				next({path: '/'})
			}
		})
		.catch(() => {
			next({path: '/login'})
		})
})
export default router
