import {
	useStore,
	mapState,
	createNamespacedHelpers,
	mapActions,
	mapMutations,
	Module,
	Mapper,
	ActionMethod,
	Computed,
	MutationMethod
} from "vuex";
import {computed} from "vue";

// eslint-disable-next-line
function useMapper(mapper: string[], func: Mapper<ActionMethod | Computed | MutationMethod>, isAction = false):any {
	const store = useStore()

	const storeStateFns = func(mapper)
	const storeState:object = {}
	Object.keys(storeStateFns).forEach((keyFn: string) => {
		const fn = storeStateFns[keyFn].bind({$store: store})
		// @ts-ignore
		storeState[keyFn] = isAction ? fn : computed(fn)
	})

	return storeState
}

interface ActionsParams {
	nameSpace?: string,
	actions: string[]
}

export const useMapActions = ({nameSpace, actions}: ActionsParams) => {
	let func = mapActions
	if (nameSpace) {
		func = createNamespacedHelpers(nameSpace).mapActions
	}
	return useMapper(actions, func, true)
}

interface MutationsParams {
	nameSpace?: string,
	mutations: string[]
}

export const useMapMutations = ({nameSpace, mutations}: MutationsParams) => {
	let func = mapMutations
	if (nameSpace) {
		func = createNamespacedHelpers(nameSpace).mapMutations
	}
	return useMapper(mutations, func, true)
}

interface StatesParams {
	nameSpace?: string,
	states: string[]
}

export const useMapState = ({nameSpace, states}: StatesParams) => {
	let func = mapState
	if (nameSpace) {
		func = createNamespacedHelpers(nameSpace).mapState
	}
	return useMapper(states, func)
}

// 动态注册store模块
export function registerModule<S, R>(path: string, module: Module<S, R>) {
	const store = useStore()
	store.registerModule(path, module)
}

// 动态卸载store模块
export const unregisterModule = (path: string): void => {
	const store = useStore()
	store.unregisterModule(path)
}