import axios, {AxiosRequestConfig, AxiosResponse} from "axios"
import qs from 'qs'
import {getIndexDB} from "@/utils/indexDB";
// import stores, {UPDATE_LOGIN_USER} from "@/stores";
import {STORAGE_KEYS} from "@/const";
// @ts-ignore
import Browser from '@/utils/browser'
// @ts-ignore
import {phoneModel} from "@/utils/device";

const browser = new Browser(navigator.userAgent).browser
// @ts-ignore
axios.defaults.headers['Content-Type'] = 'application/x-www-form-urlencoded'

interface HttpResponse {
	code: number;
	// eslint-disable-next-line
	result: any;
	msg: string | undefined;
	info?: string | undefined;
	data?: string | undefined;

	// eslint-disable-next-line
	[propName: string]: any
}

// 请求拦截
axios.interceptors.request.use(async (config: AxiosRequestConfig) => {
	try {
		// 强制登录接口过滤
		// @ts-ignore
		const {token} = await getIndexDB(STORAGE_KEYS.LOGGED_IN_USER) || {}
		const customerHeader = {
			"X-WJ-AUTHORIZATION": token
		}
		config.headers = Object.assign({}, config.headers, customerHeader)
		return config
	} catch (e) {
		// 需要终止请求或者登录失败（含用户取消收取授权等操作）
		return Promise.reject(e)
	}
})

// 请求成功
const isSuccess = (code: string | number): boolean => (!code || code === 200 || code === '000000')

// 网络请求状态200时认为请求成功，拦截处理响应数据，统一返回:{code, result, msg},在处理xhr请求时不需要在resolve函数中判断code是否为正常，即只有当服务端返回code === 200或者0时 才会掉用resolve，其他状态统一掉用reject，减少繁琐的接口错误逻辑判断，当config中包含returnAsIs并且config.returnAsIs !== false(包含其他js类型转换为boolen值的false)
const responseInterceptor = ({data}: AxiosResponse) => {
	// 原样返回不做响应拦截处理，比如说直接请求json或者其他file 时需要
	// if (responseReturn === RESPONSE_RETURN.asIs) return data
	const {code, data: result, info, msg = info, ...more} = data
	if (isSuccess(code)) {
		return {code, result, msg, ...more}
	}
	return Promise.reject({code, result, msg});
}

// 请求失败处理
// eslint-disable-next-line
const reqFileResponseInterceptor = (err: any) => {
	const error: {
		msg?: string
	} = {};
	const status: number = err.request?.status
	if (err.message === 'Network Error' && status === 0) {
		error.msg = '网络请求错误，请检查您的网络';
	} else if (status === 404) {
		error.msg = '服务器跑到火星上去了';
	} else if (status === 500) {
		error.msg = '服务器发生故障';
	} else {
		error.msg = err.toString();
	}
	return Promise.reject(error);
}

// Add a response interceptor 响应拦截
axios.interceptors.response.use(responseInterceptor, reqFileResponseInterceptor);

/**
 * post请求
 * @param url : string : 接口地址，不要包含baseURL
 * @param params : object : 请求参数
 * @param config : object : 请求配置信息，参考axios config配置项，默认不用传, 如果不需要统一拦截处理响应信息请传入
 * {
 * 	responseReturn:RESPONSE_RETURN.handle
 * 	authenticationMode:AUTHENTICATION_MODE.continue
 * }
 * @returns {Promise<HttpResponse>}
 */
export const post = async (url: string, params: object = {}, config: object = {
	// responseReturn: RESPONSE_RETURN.handle
}): Promise<HttpResponse> => {
	return axios.post(url, qs.stringify({
		...params,
		add_device: phoneModel(),
		add_browser: browser
	}), {
		baseURL: process.env.VUE_APP_BASE_URL,
		...config
	})
}

/**
 * get请求
 * @param url : string : 接口地址，不要包含baseURL
 * @param params : object : 请求参数
 * @param config : object : 请求配置信息，参考axios config配置项，默认不用传, 如果不需要统一拦截处理响应信息请传入
 * {
 * 	responseReturn:RESPONSE_RETURN.handle
 * 	authenticationMode:AUTHENTICATION_MODE.continue
 * }
 * @returns {Promise<HttpResponse>}
 */
export const get = async (url: string, params: object = {}, config: object = {
	// responseReturn: RESPONSE_RETURN.handle
}): Promise<HttpResponse> => {
	return axios.get(url, {
		baseURL: process.env.VUE_APP_BASE_URL,
		params,
		...config
	})
}

