import {App} from "vue";

const SvgIconPlugin = {
	install(app: App, {imports}: { imports: string[] } = {imports: []}) {
		const dir = `@/assets/icons/svg`
		if (imports.length > 0) {
			// 按需引入图标
			imports.forEach((name: string) => {
				require(`${dir}/${name}.svg`);
			});
		} else {
			// 全量引入图标
			const ctx = require.context('@/assets/icons/svg', false, /\.svg$/);
			ctx.keys().map(ctx);
		}
	}
}
export default SvgIconPlugin