import {createStore} from "vuex";
import {post} from "@/utils/http";
import {getIndexDB, setIndexDB} from "@/utils/indexDB";
import {STORAGE_KEYS} from "@/const";

interface LoginParams {
	number: string,
	code: string
}

// 更新用户
export const UPDATE_LOGIN_USER = `UPDATE_LOGIN_USER`

// 全局公用，不要在里面新增局部或者页面中使用的store，根据模块划分在modules目录下创建
export default createStore({
	state: {
		user: null,
	},
	getters: {},
	mutations: {
		[UPDATE_LOGIN_USER](state, payload) {
			setIndexDB(STORAGE_KEYS.LOGGED_IN_USER, payload)
			state.user = payload
		},
	},
	actions: {
		// 登录
		async login({commit}, payload: LoginParams) {
			const {result} = await post(`/user/XFUser/Login`, payload)
			commit(UPDATE_LOGIN_USER, result)
			return {result}
		},
		// 退出登录
		async logout({commit}) {
			// await deleteHttp(`/auth/logout`)
			commit(UPDATE_LOGIN_USER, null)
		},
		// 初始化登录
		async initializeLogin({commit}) {
			const payload = await getIndexDB(STORAGE_KEYS.LOGGED_IN_USER)
			if (payload) {
				commit(UPDATE_LOGIN_USER, payload)
			}
		},
		// 获取用户信息
		async queryUser({commit, state: {user}}) {
			// @ts-ignore
			const {result} = await post(`/user/XFUser/UserInfo`)
			const __user = Object.assign({}, user, result)
			commit(UPDATE_LOGIN_USER, __user)
		}
	},
	modules: {}
})
